@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

/*
@tailwind base;
@tailwind components;
@tailwind utilities;
*/

:root{
  @apply dark;
  background-color: #283141;
  margin: 0;
  padding: 0;

  /** background image */
  background-image: url("/public/imgs/background.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}


@layer components {
  .dark {
    --colour-bg-primary: #283141;
    --colour-bg-secondary: #283141;
    --colour-text-primary: #f7fafc;
    --colour-text-secondary: #e2e8f0; /*#e2e8f0*/
    --colour-text-accent: #81e6d9;
  };

  .light {
    --colour-bg-primary: #eeeeeedb;
    --colour-bg-secondary: #edf2f7;
    --colour-text-primary: #2d3748;
    --colour-text-secondary: #415568;
    --colour-text-accent: #2b6cb0;
  };
};


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
